<template>
  <form @submit.prevent="login" id="my-form"
    class="container"
    action="https://api.staticman.net/v2/entry/QueenYoung/zerostudio/master/"
      method="POST"
    >
    <my-field v-for="(field, index) in fields" :key="index"
      v-bind="field"
      :status="status[index]"
      :index="index"
      @showInput="yo"
    />

    <div class="field">
      <label for="" class="label">ÂêåÂ≠¶ÊòØÂ•≥Ë£ÖÂ§ß‰Ω¨ËøòÊòØ?</label>
      <div class="control">
        <div class="select">
          <select v-model="sex" name="fields[sex]">
            <option disabled value="">ÊÄßÂà´</option>
            <option value="Áî∑">üë®üèª</option>
            <option value="Â•≥">üë©üèª</option>
          </select>
        </div>
      </div>
    </div>


    <div class="field">
      <label for="" class="label">ÂêåÂ≠¶Âì™‰∏™Èô¢ÁöÑ?</label>
      <div class="control">
        <div class="select">
          <select v-model="college" name="fields[college]">
            <option disabled value="">Èô¢Á≥ª</option>
            <option>ËΩØ‰ª∂Èô¢</option>
            <option>ËÆ°‰ø°Èô¢</option>
            <option>Â∑•Â≠¶Èô¢</option>
            <option>ÂÖ∂‰ªñ</option>
          </select>
        </div>
      </div>
    </div>

    <div class="field">
      <label class="label">Êàë‰ª¨ÂñúÊ¨¢Â¶πÂ≠êÂ§öÁöÑÁè≠Á∫ßü§ì</label>
      <div class="control">
        <div class="select">
          <select v-model="classroom" name="fields[classroom]">
            <option disabled value="">Áè≠Á∫ß</option>
            <option v-for="n in 14" :key="n">{{n}}</option>
          </select>
        </div>
      </div>
    </div>

    <div class="field">
      <label class="label">ÂæàÈÖ∑, ÁúüÁöÑ</label>
      <div class="control">
        <textarea class="textarea" name="fields[introduce]"
        placeholder="‰ªãÁªç‰∏Ä‰∏ã‰Ω†Ëá™Â∑±" v-model="introduce"/>
      </div>
    </div>

    <div class="field">
      <p class="control">
        <button 
          :disabled="!canLogin()"
          class="button is-success"
        >Login</button>
      </p>
    </div>
  </form>
</template>

<script>
import Field from './BaseField'
import Hero from './Hero'

export default {
  components: {
    'my-field': Field,
    hero: Hero
  },
  data() {
    return {
      fields: [
        {
          iconName: 'user',
          placeholder: 'ÊØîÂ¶Ç: ‰πîÂ∏ÉÊñØ',
          type: 'text',
          labelTitle: 'ÂêçÂ≠ó',
          warning: '‰∏çË¶ÅÁî®Ëã±ÊñáÂêçËÄçÊàë',
          name: 'fields[name]'
        },
        {
          iconName: 'mobile',
          placeholder: '‰∏çË¶ÅÁûéÁªôÊâãÊú∫Âè∑Á†Å',
          type: 'tel',
          labelTitle: 'ÊâãÊú∫',
          name: 'fields[telphone]'
        }
      ],
      status: Array.apply(null, { length: 2 }),
      inputs: ['', ''],
      sex: '',
      college: '',
      classroom: '',
      introduce: ''
    }
  },
  methods: {
    clearInput() {
      this.inputs = ['', '']
      this.sex = ''
      this.college = ''
      this.classroom = ''
      this.introduce = ''
    },
    yo(input, index) {
      this.inputs.splice(index, 1, input)
      if (index === 0) {
        this.status.splice(index, 1, this.legalName(input))
      } else {
        this.status.splice(index, 1, this.legalTelphone(input))
      }
    },
    login() {
      const fields = {
        fields: [{
          name: this.inputs[0],
          telphone: this.inputs[1],
          classroom: this.classroom,
          sex: this.sex,
          introduce: this.introduce,
          college: this.college
        }]
      }
      const urls = 'https://api.staticman.net/v2/entry/QueenYoung/zerostudio/master/'

      fetch(urls, {
        method: 'POST',
        body: JSON.stringify(fields),
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json'
        }
      })
      this.clearInput()
    },
    canLogin() {
      return (
        this.status.every(Boolean) &&
        [this.sex, this.college, this.classroom, this.introduce].every(Boolean)
      )
    },
    legalTelphone(input) {
      const tel = /^1[3578]\d{9}$/
      return tel.test(input)
    },
    legalName(input) {
      if (input === '‰πîÂ∏ÉÊñØ') {
        alert('ËÄÅÂ≠êÂéª‰Ω†Â¶àÁöÑ!')
        alert('ÁªôÊàëÂ•ΩÂ•ΩÂ°´!')
        return false
      }
      const range = [0x4e00, 0x9fa5]
      const codePoints = Array.from(input, code => code.codePointAt(0))
      return codePoints.every(point => point >= range[0] && point <= range[1])
    }
  }
}
</script>






